.c-inventory {
	max-height: 78vh;
	overflow-y: auto;
}

.c-inventory__item {
	position: relative;
	margin: 18px 0px;
}

.c-inventory__item__icon {
	height: 58px;
	width: 58px;
	background-color: rgba(255, 255, 255, 0.2);
}

.c-inventory__item__details {
	position: absolute;
	left: 77px;
	top: -4px;
}

.c-inventory__item__details p {
	margin: 0px;
	padding: 0px;
}

.c-inventory__item__details--name {
	text-transform: capitalize;
}

.c-inventory__item__details--description {
	font-size: rem(10px);
}

.c-inventory__item__amount {
	position: absolute;
	left: 55px;
	bottom: -4px;
	background-color: color('dingey-light');
	width: 18px;
	height: 18px;
	text-align: center;
	line-height: 1.3;
	font-size: rem(14px);
	border-radius: 100%;
}